<template>
  <div class="content_editable" :class="{editable_focus: focused, mr152: showRadio || showCheckbox}" @click="edit" @mouseout="blur">
    <i class="icon_radio" v-if="showRadio"></i>
    <i class="icon_square" v-if="showCheckbox"></i>
    <div class="q_title" :class="{'is_pic': isPic, 'with_icon': showRadio || showCheckbox, 'show_input': (showRadio || showCheckbox) && showInput}"
      :style="{fontSize: fontSize}"
      v-html="innerText"
      contenteditable="true"
      ref="div"
      @input="changeText">
    </div>
  </div>
</template>

<script>
  import api from '~/api/api';
  //import NProgress from 'nprogress'
  export default {
    props: {
        value: {
            type: String,
            default: ''
        },
        showRadio: {
          type: Boolean,
          default: false
        },
        isPic: {
          type: Boolean,
          default: false
        },
        showCheckbox: {
          type: Boolean,
          default: false
        },
        showInput: {
          type: Boolean,
          default: false
        },
        fontSize: {
          type: String,
          default: '16px'
        }
    },
    data(){
        return {
            innerText: this.value,
            focused: false
        }
    },
    watch: {
        'value'(val){
            this.innerText = val;
        }
    },
    methods: {
        edit() {
          this.focused = true;
        },
        blur(){
            this.focused = false;
            if(this.$refs.div.innerHTML != this.value) {
              this.$emit('input', this.$refs.div.innerHTML.replace( /<[^>]*>/g, "" ));
            }
        },
        changeText(){
            // this.innerText = this.$refs.div.innerHTML;
            // this.$emit('input', this.innerText);
        }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .content_editable {
    box-sizing: border-box;
    border: solid 1px transparent;
    &:hover {
      border: dashed 1px #aaa !important;
      padding: 0;
    }
    &.editable_focus{
      background-color: #f4f4f4;
      border: solid 1px #f4f4f4 !important;
      padding: 0;
    }
    &.mr152 {
      margin-right 152px
    }

    .icon_square {
      position: absolute;
      top: 7px;
      left: 14px;
      width: 14px;
      height: 14px;
      border-radius: 2px;
      border: solid 1px #aaaaaa;
      background-color: #ffffff;
    }

    .icon_radio {
        position: absolute;
        top: 7px;
        left: 14px;
        width: 14px;
        height: 14px;
        border-radius: 50%;
        border: 1px solid #aaa;
        background-color: #fff;
    }

    .q_title {
      padding: 6px 10px;
      font-size: 16px;
      user-select: text;
      user-modify: read-write-plaintext-only;
      width: 100%
      &.show_input {
        &:after {
          content: '';
          display: inline-block;
          width: 70px;
          height: 18px;
          border: solid 1px #d8d8d8;
          border-radius: 2px;
          background-color: #ffffff;
          margin-left: 14px;
          vertical-align: top;
        }
      }
      &.is_pic {
        padding 3px;
        font-size 14px
      }
    }
    .with_icon {
      padding: 5px 10px 5px 38px;
      font-size: 14px !important;
    }
  }
</style>



