<template>
    <div class="setting_main_wrap slideInRight animated" style="opacity: 1; display: block; right: 0px;">
        <div class="question_setting">
            <div class="q_title">
                <span class="question_seq" v-if="selectedItem.oIndex == null">Q{{selectedItem.qIndex + 1}}. 题目设置</span>
                <span class="question_seq" v-if="selectedItem.oIndex != null">Q{{selectedItem.qIndex + 1}}. 选项{{selectedItem.oIndex + 1}} 的设置</span>
            </div>
            <div class="block block_question" v-if="selectedItem.oIndex == null">
                <div class="block_head">整题设置</div>
                <div class="block_mod">
                    <div class="setting_item">
                        <div class="hd">
                            <span class="hd_txt"></span>此题必答
                            <label class="btn_switch">
                                <el-switch v-model="required" active-value="1" inactive-value="0" @change="(val) => setRequired(val, 'required')" active-color="#2672ff"></el-switch>
                            </label>
                        </div>
                        <div class="hd" v-if="questions[selectedItem.qIndex].qtype == 3 || questions[selectedItem.qIndex].qtype == 307">
                            <span class="hd_title">字数限制(不限制请填0)</span>
                            <div class="hd_content">
                                <!-- <el-input v-model="alength" @blur="blur"></el-input> -->
                                <el-input-number size="mini" v-model="alength" @change="blur('alength')"></el-input-number>
                                <span style="width:60px; margin-left:20px;">个字符</span>
                            </div>
                        </div>
                        <div class="hd" v-if="questions[selectedItem.qIndex].qtype == 3 || questions[selectedItem.qIndex].qtype == 307">
                            <span class="hd_title">展示行数</span>
                            <div class="hd_content">
                                <!-- <el-input v-model="alength" @blur="blur"></el-input> -->
                                <el-input-number size="mini" v-model="rowsnum" @change="blur('rowsnum')" :min="1"></el-input-number>
                                <span style="width:60px; margin-left:20px;">行</span>
                            </div>
                        </div>
                        <div class="hd" v-if="questions[selectedItem.qIndex].qtype == 2">
                            选项随机打乱顺序
                            <label class="btn_switch">
                                <el-switch v-model="unorder" @change="(val) => setRequired(val, 'unorder')" active-value="1" inactive-value="0" active-color="#2672ff"></el-switch>
                            </label>
                        </div>
                        <!-- <div class="hd" v-if="questions[selectedItem.qIndex].qtype == 2">
                            <div class="hd_title">可选范围</div>
                            <div class="hd_select">
                            <div class="hd_select_title">最少</div>
                            <el-select v-model="value" class="select_selection min_num">
                                <el-option
                                    v-for="item in questions[selectedItem.qIndex].options.length"
                                    :key="item"
                                    :label="item + '项'"
                                    :value="item">
                                </el-option>
                            </el-select>
                            </div>
                            <div class="hd_select">
                            <div class="hd_select_title">最多</div>
                            <el-select v-model="value" class="select_selection max_num">
                                <el-option
                                    v-for="item in questions[selectedItem.qIndex].options.length"
                                    :key="item"
                                    :label="item + '项'"
                                    :value="item">
                                </el-option>
                            </el-select>
                            </div>
                        </div> -->
                    </div>
                </div>
            </div>
            <div class="block block_option" v-if="selectedItem.oIndex != null">
                <!-- <h3 class="block_head">选项设置</h3>
                <div class="block_mod">
                    <div class="setting_item">
                        <div class="hd">
                            选项布局
                            <div class="tab_layout">
                                <a class="btn_text_icon btn_layout btn-layout-single">单列</a>
                                <a class="btn_text_icon btn_layout btn-layout-single checked">2列</a>
                            </div>
                        </div>
                    </div>
                </div> -->
                <div class="block_mod">
                    <div class="setting_item collapse_box">
                        <div class="hd">
                            在选项后增加填空框
                            <label class="btn_switch">
                                <el-switch v-model="remark" @change="(val) => setRemark(val, 'remark')" active-value="1" inactive-value="0" active-color="#2672ff"></el-switch>
                            </label>
                        </div>
                        <div class="hd">
                        该空可不填
                        <label class="btn_switch">
                            <el-switch v-model="content_required" @change="(val) => setRemark(val, 'content_required')" active-value="1" inactive-value="0" :disabled="remark == 0" active-color="#2672ff"></el-switch>
                        </label>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
  import api from '~/api/api';
  import {mapState} from 'vuex';
  import * as types from '~/store/mutation-type';
  import store from '~/store/index';

  export default {
    computed: {
        ...mapState({
            selectedItem: state => state.selectedItem,
            questions: state => state.questions
        })
    },
    data() {
        return {
            required: "1",
            remark: "0",
            content_required: "0",
            unorder: "0",
            rowsnum: 1,
            alength: null
        }
    },
    watch: {
        'questions': {
            handler (val) {
                if(this.selectedItem.qIndex != null) {
                    this.required = val[this.selectedItem.qIndex].required;
                    this.alength = parseInt(val[this.selectedItem.qIndex].alength);
                    this.unorder = val[this.selectedItem.qIndex].unorder;
                    this.rowsnum = val[this.selectedItem.qIndex].rowsnum;
                }
                
                if(this.selectedItem.oIndex != null) {
                    this.remark = val[this.selectedItem.qIndex].options[this.selectedItem.oIndex].remark;
                    this.content_required = val[this.selectedItem.qIndex].options[this.selectedItem.oIndex].content_required;
                }
            },
            deep: true,
            immediate: true,
        },
        'selectedItem': {
            handler (val) {
                if(val.qIndex != null) {
                    this.required = this.questions[val.qIndex].required;
                    this.alength = parseInt(this.questions[val.qIndex].alength);
                    this.unorder = this.questions[val.qIndex].unorder;
                    this.rowsnum = this.questions[val.qIndex].rowsnum;
                }
                if(val.oIndex != null) {
                    this.remark = this.questions[val.qIndex].options[val.oIndex].remark;
                    this.content_required = this.questions[val.qIndex].options[val.oIndex].content_required;
                }
            },
            deep: true,
            immediate: true,
        }
    },
    mounted() {
        if(this.selectedItem && this.questions) {
            if(this.selectedItem.qIndex != null) {
                this.required = this.questions[this.selectedItem.qIndex].required;
                this.alength = parseInt(this.questions[this.selectedItem.qIndex].alength);
                this.unorder = this.questions[this.selectedItem.qIndex].unorder;
                this.rowsnum = this.questions[this.selectedItem.qIndex].rowsnum;
            }
            if(this.selectedItem.oIndex != null) {
                this.remark = this.questions[this.selectedItem.qIndex].options[this.selectedItem.oIndex].remark;
                this.content_required = this.questions[this.selectedItem.qIndex].options[this.selectedItem.oIndex].content_required;
            }
        }
    },
    methods: {
        setRequired(val, key) {
            let obj = {};
            obj[key] = val;
            let data = Object.assign({}, this.questions[this.selectedItem.qIndex], obj);
            this.updateQuestion(data);
        },
        blur(key) {
            let question = JSON.parse(JSON.stringify(this.questions[this.selectedItem.qIndex]));
            question[key] = this[key] ? this[key] : 0;
            let data = Object.assign({}, this.questions[this.selectedItem.qIndex], question);
            this.updateQuestion(data);
        },
        setRemark(val, key) {
            let options = JSON.parse(JSON.stringify(this.questions[this.selectedItem.qIndex].options));
            options[this.selectedItem.oIndex][key] = val;
            if(key == 'remark' && val == 0) {
                options[this.selectedItem.oIndex].content_required = "0";
            }
            let data = Object.assign({}, this.questions[this.selectedItem.qIndex], {
                options
            });
            this.updateQuestion(data);
        },
        updateQuestion(data) {
            api.saveQuestion(data).then(res => {
                if(res) {
                    store.commit(types.UPDATE_QUESTIONS, {
                        index: this.selectedItem.qIndex,
                        data: res.data
                    });
                }
            });
        }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .setting_main_wrap {
    display: none;
    width: 260px;
    overflow: auto;
    background-color: #ffffff;
    border-left: solid 1px #dde5ed;
    position: fixed;
    right: -260px;
    top: 66px;
    bottom: 0;
    padding: 30px 0;
    line-height: 1.38;
    .q_title {
        display: inline-block;
        color: #ffffff;
        font-size: 14px;
        padding: 5px 12px;
        border-radius: 2px;
        background-color: #495165;
        margin-left: 18px;
    }
    .block_head {
        margin-left: 18px;
    }
    .block {
        margin-top: 30px;

        .block_head {
            color: #484848;
            font-size: 16px;
        }
        .block_mod {
            margin-top: 14px;
        }
        .setting_item {
            color: #707070;
            font-size: 13px;
            line-height: 24px;
            .hd {
                padding: 12px 18px 12px 0;
                margin-left: 18px;
                .hd_select {
                    margin: 8px 0 0;
                    display flex
                    align-items: center
                }
                &_content {
                    margin-top: 10px;
                    display flex
                    align-items: center
                    height 32px
                }
                &:first-child {
                    border-top: solid 1px #dde5ed;
                }
                .hd_txt {
                    display: inline-block;
                }
                .btn_switch {
                    float: right;
                }
            }
            &:last-child {
                .hd {
                    border-bottom: solid 1px #dde5ed;
                }
            }
        }
        .tab_layout {
            border-radius: 2px;
            overflow: hidden;
            vertical-align: middle;
            margin: 0 0 0 10px;
            font-size: 0;
            float: right;

            .btn_layout {
                display: inline-block;
                border-left: solid 1px #aaaaaa;
                border-top: solid 1px #aaaaaa;
                border-bottom: solid 1px #aaaaaa;
                background-color: #ffffff;
                width: 46px;
                text-align: center;
                font-size: 12px;
                line-height: 22px;
                vertical-align: middle;
                cursor: pointer;
                &.checked {
                    color: #ffffff;
                    background-color: #2672ff;
                    border-color: #2672ff;
                }
                &:first-child {
                    border-top-left-radius: 2px;
                    border-bottom-left-radius: 2px;
                }
                &:last-child {
                    border-right: solid 1px #aaaaaa;
                    border-top-right-radius: 2px;
                    border-bottom-right-radius: 2px;
                }
            }
        }
        .btn_text {
            float: right;
        }
    }
    }
</style>



