<template>
  <div class="question_type_wrap" style="overflow: visible">
    <div class="" style="max-height: none;" tabindex="0">
      <div class="" style="position:relative; top:0; left:0;" dir="ltr">
        <div class="question_type_dl">
          <dl class="type_module">
            <dt>基本题型</dt>
            <dd>
                <label class="type_item three_letter_spacing" @click="addItem(item)" v-for="item in basic" :key="item.name">
                    <i class="icon_wj" :class="'type_item_' + item.qtype"></i>{{item.name}}
                </label>
                <!-- <label class="type_item three_letter_spacing">
                    <i class="icon_wj icon_type_dropdown"></i>下拉题
                </label> -->
            </dd>
          </dl>
          <dl class="type_module">
            <dt>常用题型</dt>
            <dd>
                <!-- <label class="type_item two_letter_spacing ">
                    <i class="icon_wj icon_type_name"></i>姓名
                </label> -->
                <!-- <label class="type_item two_letter_spacing">
                    <i class="icon_wj icon_type_mobile"></i>手机
                </label>
                <label class="type_item two_letter_spacing">
                    <i class="icon_wj icon_type_email"></i>邮箱
                </label> -->
                <label class="type_item two_letter_spacing" v-for="item in common" :key="item.name" @click="addItem(item)">
                    <i class="icon_wj" :class="'type_item_' + item.qtype"></i>{{item.name}}
                </label>
            </dd>
          </dl>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import api from '~/api/api';
  import editable from '~/components/editable.vue'
  import * as types from '~/store/mutation-type';
  import store from '~/store/index';
  import {mapState} from 'vuex';
  import qtypes from './questiontypes';

  export default {
    data() {
      return {
        basic: qtypes.basic,
        common: qtypes.common
      };
    },
    computed: {
        ...mapState({
            selectedItem: state => state.selectedItem,
            questions: state => state.questions
        })
    },
    mounted() {
      console.log(qtypes);
    },
    methods: {
      addItem(item) {
        let data = {
          sid: this.$route.params.sid,
          qtype: item.qtype,
          subject: item.subject,
          alength: 0,
          options: item.options,
          required: "1",
        }
        if(item.rows) {
          data.rows = item.rows;
        }

        api.saveQuestion(data).then(res => {
          if(res) {
            let finalItem = Object.assign({}, {
              sid: this.$route.params.sid,
              alength: 0,
              required: "1",
            }, item);
            finalItem.qid = res.data.qid
            store.commit(types.ADD_QUESTIONS, finalItem);
          }
        });
      },
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .question_type_wrap{
    width: 233px;
    height: 100%;
    overflow: auto;
    background-color: #fafafa;
    position: fixed;
    top: 66px;
    left: 72px;
    z-index: 2;
    border-right: 1px solid #dde5ed;
    .question_type_dl{
      margin: 0 0px 20px 12px;
      padding-bottom: 20px;
    }
    .type_module{
      padding: 2px 0 0;
      dt{
        color: #4a4a4a;
        font-size: 14px;
        margin-top:6px;
        font-weight:normal; 
      }
      dd{
        margin: 6px 0 0;
        font-size: 0;
        .type_item{
          display: inline-block;
          width: 96px;
          height: 30px;
          line-height: 30px;
          color: #707070;
          font-size: 12px;
          padding: 1px;
          border: solid 1px #d8d8d8;
          background-color: #ffffff;
          border-radius: 2px;
          margin: 0 6px 6px 0;
          cursor: pointer;
          vertical-align: middle;
          box-sizing: content-box;
          .icon_wj {
            display: inline-block;
            width: 24px;
            height: 24px;
            background: url(./icon_edit_question.png) no-repeat;
            background-size: 135px;
            vertical-align: top;
            margin: 3px;
          }
          .type_item_1 {
            background-position: -8px -7px;
          }
          .type_item_2 {
            background-position: -36px -7px;
          }
          .type_item_3 {
            background-position: -8px -39px;
          }
          .type_item_5 {
            background-position: -36px -39px;
          }
          .type_item_6 {
            background-position: -8px -353px;
          }
          .type_item_102 {
            background-position: -8px -71px;
          }
          .icon_type_dropdown {
            background-position: -8px -135px;
          }
          .icon_type_name {
            background-position: -8px -164px;
          }
          .icon_type_mobile {
            background-position: -36px -164px;
          }
          .icon_type_email {
            background-position: -8px -196px;
          }
          .type_item_101 {
            background-position: -36px -196px;
          }
          .type_item_305 {
            background-position: -8px -228px;
          }
          .type_item_307 {
            background-position: -36px -228px;
          }
          .type_item_401 {
            background-position: -8px -289px;
          }
          &:hover {
            padding: 0;
            border: solid 2px #2672ff;
            color: #2672ff;
            .type_item_1{
              background-position: -72px -7px;
            }
            .type_item_2{
              background-position: -100px -7px;
            }
            .type_item_3{
              background-position: -72px -39px;
            }
            .type_item_5{
              background-position: -100px -39px;
            }
            .type_item_6 {
              background-position: -72px -353px;
            }
            .type_item_102{
              background-position: -72px -71px;
            }
            .icon_type_dropdown{
              background-position: -72px -135px;
            }
            .icon_type_name{
              background-position: -72px -164px;
            }
            .icon_type_mobile{
              background-position: -100px -164px;
            }
            .icon_type_email{
              background-position: -72px -196px;
            }
            .type_item_101{
              background-position: -100px -196px;
            }
            .type_item_305{
              background-position: -72px -228px;
            }
            .type_item_307{
              background-position: -100px -228px;
            }
            .type_item_401{
              background-position: -72px -289px;
            }
          } 
        }
      }
      &:nth-child(1){
        dt {
          margin-top:24px;
        }
      }
    }
  }
</style>



