<template>
  <div class="question"
    :class="{'question_focus': selectedItem.qIndex == index}"
    @click="setQuestionFocused()">
      <div class="q_content_wrap">
          <div class="q_content">
              <div class="q_title_wrap">
                  <div class="q_seq">{{index + 1}}</div>
                  <editable :value="item.subject" v-on:input="(val) => updateSubject(val)"></editable>
              </div>
              <div class="q_option_list" :class="{'is_pic': item.qtype == 5}">
                  <!-- 选择题 -->
                  <ul class="q_option_ul" v-if="/^[1|2|4]/.test(item.qtype)">
                      <li class="option_item" v-for="(option, oIndex) in item.options" :key="option.oid">
                          <div class="option_title_wrap">
                              <editable 
                                :value="option.content"
                                v-on:input="(val) => updateContent(oIndex, val)"
                                :show-radio="/^[1|4]/.test(item.qtype)"
                                :show-checkbox="item.qtype == 2"
                                :show-input="option.remark == 1"
                              >
                              </editable>
                              <div class="option_operate">
                                  <a class="icon_wj btn_icon btn_option_setting" @click="showOptionSetting($event, oIndex)"></a>
                                  <a class="icon_wj btn_icon btn_option_del" @click="deleteOption(oIndex)"></a>
                              </div>
                          </div>
                      </li>
                  </ul>
                  <!-- 填空题 -->
                  <div class="blanksframe" v-if="item.qtype == 3 || item.qtype == 307"></div>
                  <!-- 图片选择题 -->
                  <ul class="q_option_img_ul" v-if="item.qtype == 5">
                    <li class="option_item" v-for="(option, oIndex) in item.options" :key="option.oid">
                        <div class="option_img_box" style="display: block;">
                            <img :src="option.pic">
                            <a class="img_handle" @click="deleteOption(oIndex)">
                              <i class="el-icon-delete"></i>
                            </a>
                        </div>
                        <div class="option_title_wrap" style="display: block;">
                            <i class="icon_radio"></i>
                            <editable :is-pic="true" font-size="14px" :value="option.content" v-on:input="(val) => updateContent(oIndex, val)"></editable>
                        </div>
                    </li>
                  </ul>
                  
                  <table v-if="item.qtype == 6" class="q_matrix_score">
                    <thead>
                        <tr>
                            <th v-if="ismultiple"></th>
                            <th
                              :style="{'width': (ismultiple ? 80 : 100)/(item.options.length) + '%'}"
                              v-for="(option, oIndex) in item.options"
                              :key="oIndex">
                                <div class="star_tips">
                                  <editable 
                                    :value="option.content"
                                    font-size="14px"
                                    v-on:input="(val) => updateContent(oIndex, val)"
                                  ></editable>
                                  <a class="icon_wj btn_option_del" @click="deleteOption(oIndex)"></a>
                                </div>
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                      <tr class="matrix_item" v-for="(dimension, dIndex) in item.rows" :key="dIndex">
                        <td class="sub_title" v-if="ismultiple">
                            <div class="subtitle_text">
                                <editable 
                                  :value="dimension.content"
                                  font-size="14px"
                                  v-on:input="(val) => updateRowContent(dIndex, val)"
                                ></editable>
                                <a class="icon_wj btn_option_del" @click="deleteRow(dIndex)"></a>
                            </div>
                        </td>
                        <td v-for="(option, oIndex) in item.options" :key="oIndex" style="position: relative">
                            <div class="icon_radio_matrix"></div>
                        </td>
                      </tr>
                    </tbody>
                  </table>

                  <!-- 省市区题 -->
                  <div v-if="item.qtype == 305" class="q_option_ul">
                    <select-addr class="address"></select-addr>
                  </div>
              </div>
              <!-- 加选项 选择题-->
              <div class="q_option_operate" v-if="/^[1|2|4]/.test(item.qtype)">
                  <a class="btn_text_icon btn_add_single" @click="addCommonOption()">
                      <i class="icon_wj icon_add"></i>添加单个选项
                  </a>
                  <a class="btn_text_icon btn_add_more" @click="dialogVisible = true">
                      <i class="icon_wj icon_add_more"></i>批量添加选项
                  </a>
                  <a class="btn_text_icon btn_add_other" @click="addOtherOption()">
                      <i class="icon_wj icon_add_other"></i>添加「其他」项
                  </a>
              </div>
              <div class="q_option_operate" v-if="item.qtype == 6">
                  <a class="btn_text_icon btn_add_single" @click="addMatrixOption()">
                      <i class="icon_wj icon_add"></i>添加打分
                  </a>
                  <a class="btn_text_icon btn_add_other" @click="addMatrixRow()">
                      <i class="icon_wj icon_add_other"></i>添加维度
                  </a>
              </div>
              <!-- 加选项 图片选择题-->
              <div class="q_option_operate" v-if="item.qtype == 5">
                  <a class="btn_text_icon btn_add_single">
                    <i class="icon_wj icon_add"></i>上传多张图片生成选项
                    <input type="file" name="files[]" multiple="multiple" @change="uploadFile">
                  </a>
              </div>
          </div>
      </div>
      <div class="q_operate">
          <a class="icon_wj btn_icon btn_question_sort"></a>
          <a class="icon_wj btn_icon btn_question_clone" @click="copyQuestion($event)"></a>
          <a class="icon_wj btn_icon btn_question_del" @click="showDeleteDialog($event)"></a>
      </div>
      <el-dialog
        title="批量添加选项"
        :visible.sync="dialogVisible"
        :close-on-click-modal="false"
        width="30%">
        <div>
          <div style="margin-bottom: 15px;">每行代表一个选项，可以添加多个选项</div>
          <el-input type="textarea" rows="10" v-model="options"></el-input>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="addMoreOptions">保 存</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
  import api from '~/api/api';
  import editable from '~/components/editable.vue'
  import selectAddr from '~/components/selectAddr.vue'
  import * as types from '~/store/mutation-type';
  import store from '~/store/index';
  import {mapState} from 'vuex';

  export default {
    props: ['index', 'item'],
    components: {
      editable,
      selectAddr
    },
    data() {
      return {
        dialogVisible: false,
        ismultiple: true,
        options: ''
      }
    },
    computed: {
        ...mapState({
            selectedItem: state => state.selectedItem,
            questions: state => state.questions
        })
    },
    watch: {
      'item': {
          handler (val) {
            if(val.rows && val.rows.length) {
              this.ismultiple = val.rows.length >= 2 || val.rows[0].content;
            }
          },
          deep: true,
          immediate: true,
      },
    },
    mounted() {
      // console.log(this.item.options);
    },
    methods: {
      uploadFile(e, index) {
        api.upload(e.target.files).then(res => {
          if (res) {
            this.addOption(res.data);
          }
        });
      },
      copyQuestion() {
        api.copyQuestion(this.$route.params.sid, this.item.qid).then(res => {
          if (res) {
            store.commit(types.INSERT_QUESTIONS, {
              index: this.index + 1,
              data: res.data
            });
            store.commit(types.SET_SELECTED_ITEM, {qIndex: this.index + 1})
          }
        })
      },
      showDeleteDialog(e) {
        e.stopPropagation();
        this.$confirm('确定要删除吗？', '删除题目确认', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.deleteQuestion();
        });
      },
      deleteQuestion() {
        api.delelteQuestion(this.$route.params.sid, this.item.qid).then(res => {
          if (res) {
            store.commit(types.DELETE_QUESTIONS, this.index);
            store.commit(types.SET_SELECTED_ITEM, {qIndex: null})
          }
        })
      },
      setQuestionFocused() {
        store.commit(types.SET_SELECTED_ITEM, {qIndex: this.index})
      },
      showOptionSetting(e, oIndex) {
        e.stopPropagation();
        store.commit(types.SET_SELECTED_ITEM, {qIndex: this.index, oIndex})
      },
      updateQuestion(data) {
        api.saveQuestion(data).then(res => {
          if(res) {
            this.dialogVisible = false;
            store.commit(types.UPDATE_QUESTIONS, {
              index: this.index,
              data: res.data
            });
          }
        });
      },
      updateSubject(val) {
        let data = Object.assign({}, this.questions[this.index], {
            subject: val
        });
        this.updateQuestion(data);
      },
      deleteOption(oIndex) {
        this.$confirm('确定要删除吗？', '删除确认', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let options = JSON.parse(JSON.stringify(this.questions[this.index].options));
          options.splice(oIndex, 1);

          let data = Object.assign({}, this.questions[this.index], {
              options
          });
          this.updateQuestion(data);
        });
      },
      deleteRow(dIndex) {
        let rows = JSON.parse(JSON.stringify(this.questions[this.index].rows));
        rows.splice(dIndex, 1);

        let data = Object.assign({}, this.questions[this.index], {
            rows
        });
        this.updateQuestion(data);
      },
      updateRowContent(dIndex, val) {
        let rows = JSON.parse(JSON.stringify(this.questions[this.index].rows));
        rows[dIndex].content = val;

        let data = Object.assign({}, this.questions[this.index], {
            rows
        });
        this.updateQuestion(data);
      },
      addMoreOptions() {
        let options = this.options.split(/[\n,]/g);
        let data = options.map(x => {
          return {
            content: x,
            pic: "",
            remark: "0"
          };
        })
        this.addOption(data);
      },
      addOption(optionArr) {
        let options = JSON.parse(JSON.stringify(this.questions[this.index].options));
        options = options.concat(optionArr);

        let data = Object.assign({}, this.questions[this.index], {
            options
        });
        this.updateQuestion(data);
      },
      addMatrixRow() {
        let rows = JSON.parse(JSON.stringify(this.questions[this.index].rows));
        rows = rows.concat([{
          "content": `维度${this.questions[this.index].rows.length + 1}`,
        }]);

        let data = Object.assign({}, this.questions[this.index], {
            rows
        });
        this.updateQuestion(data);
      },
      addMatrixOption() {
        this.addOption([{
          "content": `打分${this.questions[this.index].options.length + 1}`,
        }]);
      },
      addCommonOption() {
        this.addOption([{
          "content": `选项${this.questions[this.index].options.length + 1}`,
          "pic": "",
          "remark": "0"
        }]);
      },
      addOtherOption() {
        this.addOption([{
          "content": '其他',
          "pic": "",
          "remark": "1",
          "content_required": "0"
        }]);
      },
      updateContent(oIndex, val) {
        let options = JSON.parse(JSON.stringify(this.questions[this.index].options));
        options[oIndex].content = val;

        let data = Object.assign({}, this.questions[this.index], {
            options
        });
        this.updateQuestion(data);
      },
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .question{
    margin: 12px 0;
    border-radius: 4px;
    background-color: #ffffff;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
    position: relative;
    .cropper-container {
      height: 500px
    }
    .primary {
      background-color: #2672ff;
      border-color: #2672ff;
    }
    .btn_icon {
      margin-left: 15px;
    }
    .icon_radio {
        position: absolute;
        top: 11px;
        left: 8px;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        border: solid 1px #aaaaaa;
        background-color: #ffffff;
    }
    .icon_radio_matrix {
      width: 16px;
      height: 16px;
      border-radius: 50%;
      border: solid 1px #aaaaaa;
      background-color: #ffffff;
      left: 50%;
      position: absolute;
      margin-left: -8px;
      top: 50%;
      margin-top: -8px;
    }
    .icon_wj {
      display: inline-block;
      width: 24px;
      height: 24px;
      background: url(../images/icons.png) no-repeat;
      background-size: 520px auto;
      vertical-align: middle;
    }
    .btn_option_del {
        background-position: -48px -48px;
        &:hover {
            background-position: -72px -48px;
        }
    }
    .display_when_hover {
      display: none;
      &:hover {
        display: inline-block
      }
    }
    &.question_focus {
      box-shadow: 0 10px 15px 0 rgba(0, 0, 0, 0.1);
      border: solid 2px #2672ff;
      outline: none;
      margin: 24px 0;
      .q_operate {
        a {
          display: inline-block;
        }
      }
      .q_content {
        .q_option_operate {
          display: block;
        }
      }
    }
    .q_operate {
      position: absolute;
      right: 40px;
      top: 22px;
      .btn_question_del {
        background-position: -144px -24px;
        &:hover {
          background-position: -168px -24px;
        }
      }
      .btn_question_sort {
        background-position: 0 -24px;
        &:hover {
          background-position: -24px -24px;
        }
      }
      .btn_question_clone {
        background-position: -96px -24px;
        &:hover {
          background-position: -120px -24px;
        }
      }
      
      a {
        display: none;
      }
    }
    .q_content {
      padding: 18px 40px 18px 30px;
      .q_option_ul {
        // 一行单列
        &-1 {
          padding-right: 184px;
          li {
            width: 100%;
          }
        }
        // 一行两列
        &-2 {
          padding-right: 0;
          margin-right: -20px;
          li {
            width: 50%;
            display: inline-block;
            vertical-align: top;
          }
        }
        .address {
          margin-left: 35px;
          width: 300px;
        }
        li {
          margin: 6px 0 0;
          padding: 0 0 0 24px;
          position: relative;
          box-sizing: border-box;
          .option_title_wrap {
            position: relative;
            &:hover {
              .option_operate {
                display: block
              }
            }
          }
          .option_operate {
            display: none;
            position: absolute;
            width: 152px;
            right: 0;
            top: 0;
            padding: 4px 0;
            .btn_option_setting {
              background-position: -192px -72px;
              &:hover {
                background-position: -192px -96px;
              }
            }
            .btn_icon {
              margin-left: 20px;
            }
          }
        }
      }
      .q_matrix_score {
        width: 80%
        text-align center
        font-size 14px
        color #484848
        margin-left 38px;
        .matrix_item {
          height: 50px
        }
        .sub_title {
          width: 20%;
          min-width: 50px;
          max-width: 175px;
          padding-top: 10px;
          padding-bottom: 10px;
          .subtitle_text {
            display: flex;
            align-items: center
            line-height: 20px;
            text-align left
            a {
              display none;
            }
            &:hover {
              a {
                display: inline-block
              }
            }
          }
        }
        .star_tips {
          overflow: hidden;
          font-size: 14px;
          color: #666;
          vertical-align: middle;
          line-height: 1.5;
          font-weight: normal;
          a {
            display: none;
          }
          &:hover {
            a {
              display inline-block
            }
          }
          .content_editable {
            font-size 14px
            color #484848
            display: inline-block
          }
        }
      }
      .q_option_img_ul {
        margin: 0 0 0 40px;
        li {
            display: inline-block;
            width: 140px;
            border-width: 1px;
            border-radius: 2px;
            padding: 0;
            margin: 18px 10px 0;
            box-sizing: border-box;
            vertical-align: top;
            position: relative;
            background-color: #ffffff;
        }
        .option_item {
            border-style: solid;
            border-color: #d8d8d8;
            .option_img_box {
                width: 100%;
                position: relative;
                &:hover {
                  .img_handle {
                    display: flex
                  }
                }
                .img_handle {
                    position: absolute;
                    background-color: rgba(0,0,0,0.7);
                    width: 100%;
                    height: 100%
                    justify-content center;
                    align-items: center;
                    font-size: 32px
                    color: #2672ff
                    display: none
                    top 0px
                }
                img {
                    width: 100%;
                    border-radius: 2px;
                }
            }
            .option_title_wrap {
                padding: 6px 3px 3px 32px;
                position: relative;
            }
        }
        .option_add {
            border-style: dashed;
            border-color: #aaaaaa;
            text-align: center;
            vertical-align: middle;
            color: #aaaaaa;
            font-size: 14px;
            cursor: pointer;
            div {
                width: 138px;
                height: 194px;
                position: relative;
            }
            input[type=file] {
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
                opacity: 0;
                cursor: pointer;
            }
            .icon_add_big {
                display: inline-block;
                width: 60px;
                height: 60px;
                background-position: -10px -252px;
            }
            .add_tip {
                display: inline-block;
                margin: 24px 0 0;
            }
            .option_add_txt {
                display: inline-block;
                width: 96px;
                height: 122px;
                position: absolute;
                left: 50%;
                top: 50%;
                margin: -61px 0 0 -48px;
            }
            &:hover {
                color: #2672ff;
                border-color: #2672ff;
                .icon_add_big {
                    background-position: -90px -252px;
                }
            }
        }
      }
      .q_option_list {
        margin: 6px 0 0 16px;
        &.is_pic {
          margin: 0
        }
        .blanksframe{
          width: 440px;
          height: 32px;
          line-height: 20px;
          border-radius: 2px;
          background-color: #fff;
          border: 1px solid #d8d8d8;
          position: relative;
          margin-left: 35px;
          font-size: 14px;
          padding: 5px 10px;
          box-sizing: border-box;
          color: #484848;
          overflow: hidden;
        }
      }
      .q_title_wrap {
        position: relative;
        padding-left: 40px;
        margin-right: 110px;
        .q_seq {
            position: absolute;
            left: 0;
            top: 6px;
            color: #484848;
            font-size: 18px;
        }
      }
      .q_option_operate {
        padding: 24px 0 0 44px;
        display: none;
        color: #2672ff;
        font-size: 14px
        .btn_add_single {
          position relative
          input[type=file] {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            opacity: 0;
            cursor: pointer;
          }
        }
        .icon_add {
          background-position: -96px 0;
        }
        .icon_add_other {
          background-position: -480px -192px;
        }
        .icon_add_more {
          background-position: -96px -49px;
        }
        .btn_text_icon {
          padding: 3px 8px 3px 6px;
          margin-right: 18px;
          vertical-align: middle;
          .icon_wj {
            margin: 0 6px 0 0;
            vertical-align: top;
          }
          &:hover {
            background #f4f4f4;
          }
        }
      }
    }
    &:hover {
      .q_operate {
        a {
          display: inline-block;
        }
      }
    }
  }
</style>



