<template>
  <div class="list-template-container">
    <div class="content">
      <question-type></question-type>
      <div class="survey_main_wrap">
        <div class="survey_main">
          <!-- <div class="survey_prefix_wrap" @click="resetQuestionSelectStatus">
            <div class="survey_title">
              <editable v-model="title" font-size="20px"></editable>
            </div>
            <div class="survey_prefix">
              <editable v-model="desc"></editable>
            </div>
          </div> -->
          <div class="survey_question_wrap">
            <div class="question_null" v-if="questions.length == 0">
              <div class="question_null_tip"><i class="icon_wj icon_click"></i>点击题型以添加题目</div>
            </div>
            <div v-if="questions.length > 0">
              <draggable v-model="questions" @start="drag = true">
                <question v-for="(item, index) in questions" :key="index" :index="index" :item="item"></question>
              </draggable>
            </div>
          </div>
          <div class="survey_suffix_wrap" @click="resetQuestionSelectStatus">
            <div class="random">
              <el-checkbox v-model="checked" @change="updaeOrder">是否乱序生成问卷</el-checkbox>
            </div>
            <div class="release_share_box">
              <button class="release_share_btn" @click="updateStatus()">发布</button>
            </div>
          </div>
        </div>
      </div>
      <!-- 设置 -->
      <setting
        v-if="selectedItem.qIndex != null && selectedItem.oIndex == null"
      ></setting>
    </div>
  </div>
</template>

<script>
  import api from '~/api/api';
  import editable from '~/components/editable.vue'
  import setting from '~/components/setting.vue'
  import question from '~/components/question.vue'
  import questionType from '~/components/questionType.vue'
  import * as types from '~/store/mutation-type';
  import store from '~/store/index';
  import {mapState} from 'vuex';
  import draggable from 'vuedraggable'

  export default {
    data() {
      return {
        title: '问卷标题',
        selectedQuestion: null,
        desc: '感谢您能抽出几分钟时间来参加本次答题，现在我们就马上开始吧！',
        checked: false,
        drag: false
      };
    },
    components: {
      editable,
      setting,
      question,
      questionType,
      draggable,
    },
    computed: {
        ...mapState({
            selectedItem: state => state.selectedItem,
        }),
        questions: {
            get() {
                return store.state.questions
            },
            set(value) {
                let sData = value.map(x => x.qid);
                let data = {};
                sData.forEach((item, index) => {
                  data[item] = index + 1;
                });
                api.sort(this.$route.params.sid, data).then(res => {
                  store.commit(types.SET_QUESTIONS, value)
                });
            }
        }
    },
    mounted() {
      this.init();
    },
    methods: {
      resetQuestionSelectStatus() {
        store.commit(types.SET_SELECTED_ITEM, {qIndex: null});
      },
      groupBy(array, f) {
        const groups = {};
        array.forEach(function (o) {
          const group = JSON.stringify(f(o));
          groups[group] = groups[group] || [];
          groups[group].push(o);
        });
        return Object.keys(groups).map(function (group) {
          return groups[group];
        });
      },
      updateStatus() {
        for (let index = 0; index < this.questions.length; index++) {
          const element = this.questions[index];
          if (/[1|2|4|5]/.test(element.qtype) && this.groupBy(element.options, item => item.content).some(arr => arr.length >= 2)) {
            this.$notify.error({
              offset: 50,
              title: `第${index + 1}题有选项重复，请调整~`
            });
            return;
          }
        }
        if(this.groupBy(this.questions, item => item.subject).some(arr => arr.length >= 2)) {
          this.$notify.error({
            offset: 50,
            title: '您的问卷有重复题目，请调整~'
          });
          return;
        }
        if(!this.questions.some(x => x.required == 1)) {
          this.$notify.error({
            offset: 50,
            title: '请至少选择一道题必答~'
          });
          return;
        }
        api.updateSurvery({sid: this.$route.params.sid, status: 1}).then(res => {
          if (res) {
            this.$notify({
              offset: 50,
              title: '发布成功',
              type: 'success'
            });
          }
        })
      },
      updaeOrder() {
        api.updateSurvery({sid: this.$route.params.sid, unorder: this.checked ? 1 : 0})
      },
      init() {
        if(!this.$route.params.sid) return;
        api.getInfo(this.$route.params.sid).then(res => {
          if(!res) return;
          this.checked = res.data.unorder == 1 ? true : false
          if(!res.question || this.questions.length) return;
          res.question.forEach(item => {
            store.commit(types.ADD_QUESTIONS, item);
          });
        });
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .list-template-container {
    min-width 1306px
    margin 0 auto
    .content {
      .survey_main_wrap {
        position: relative;
        padding: 0 260px 30px 302px;
        line-height: 1.4;
        .survey_main {
          margin: 0 18px;
          min-width: 760px;
          
          .survey_title {
            .title_content{
              padding: 6px 10px;
              color: #484848;
              font-size: 20px;
            }
          }
          .survey_question_wrap{
            margin: 12px 0;
            .flip-list-move {
              transition: transform 0.5s;
            }
            .question_null {
              height: 300px;
              .question_null_tip{
                vertical-align: middle;
                text-align: center;
                line-height: 300px;
                border: dashed 1px #2672ff;
                border-radius: 4px;
                color: #484848;
                font-size: 16px;
                vertical-align: middle;
                .icon_wj {
                  vertical-align: text-top;
                }
                .icon_click {
                  background-position: -120px 0;
                  margin-right: 10px;
                }
              }
            }
          }
          .survey_suffix_wrap{
            border-radius: 4px;
            background-color: #ffffff;
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
            padding: 22px 0px 18px;
            .random {
              padding-left: 30px;
              font-size: 16px;
            }
            .release_share_box{
              height: 32px;
              line-height: 32px;
              margin-top:24px;
              text-align: center;
              padding: 0px 70px 30px;
              .release_share_btn{
                width: 112px;
                height: 32px;
                border-radius: 2px;
                background-color: #2672ff;
                line-height: 32px;
                text-align: center;
                color: #fff;
                font-size: 14px;
                cursor: pointer;
                border:none;
              }
            }
          }
          .survey_prefix_wrap{
            border-radius: 4px;
            background-color: #ffffff;
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
            padding: 18px 70px;
          }
          .survey_prefix{
            margin: 6px 0 0;
            .prefix_content{
              padding: 6px 10px;
              color: #484848;
              font-size: 16px;
            }
          }
          .icon_wj {
            display: inline-block;
            width: 24px;
            height: 24px;
            background: url(../../../images/icons.png) no-repeat;
            background-size: 520px auto;
            vertical-align: middle;
          }
        }
      }
    }
  }
</style>



